import React, { Component } from 'react'

function getIconName(inclusion){
    switch(inclusion){
        case "Flights":
            return "flight_takeoff";
        case "Hotel":
            return "hotel";
        case "Meals":
            return "restaurant_menu";
        case "Sighseeing":
            return "camera_alt"
        default:
            return "brightness_7"                
    }
}

class PackageItem extends Component{

    handleHelp = ()=>{
        window.open("https://api.whatsapp.com/send?phone=+919818040401","_blank")
    }

    render(){
        let { name, price, inclusions, scheduleString, imageUrls, days, night } = this.props.data;
        return (
            <div className="package-item row">
                <div className="col s12 m9 row package-data">
                    <div className="col s12 m4">
                        <img src="/images/agra.jpg" className="package-image" />
                    </div>
    
                    <div className="col s12 m8 package-item-content">
                        <div className="header">
                            <a href="/"><h6>{ name }</h6></a>
                            <h6>{`${night} Nights & ${days} Days`}</h6>
                        </div>
    
                        <div className="path">
                            <p>Schedule: { scheduleString }</p>
                        </div>
    
                        <div style={{marginLeft:'8px'}}>
                            <h6>Inclusions</h6>
                            <div className="inclusions">
                                {
                                    inclusions.map((inclusion,index)=>{
                                        return (
                                            <div className="inclusion" key={`${index}`}>
                                                <span><i className="material-icons">{ getIconName(inclusion.name) }</i></span>
                                                <p>{ inclusion.name }</p>
                                            </div>                                       
                                        )
                                    })
                                }
                            </div>
                        </div>
    
                    </div>
                </div>
    
                <div className="col s12 m3 tour-item-book">
                    <div className="data">
                        <h5>₹ { price }</h5>
                        <p>Starting price per adult</p>
                        <div>
                            <button onClick={this.handleHelp}>Need Help ?</button>
                            <button>Book Online</button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default PackageItem;